import React from 'react';
import "../styles/HomePage.css"
import image from "../img/logo.png"
import { NavLink } from 'react-router-dom';

const HomePage = () => {
    return (
        <div className='body'>
            <header>
                <img src={image} alt="Logo" className="logo" />
            </header>
            <main>
                <p>Nous sommes un groupe d'tudiants en 3me anne de BUT MMI  Castres et nous avons labor un projet pour la Nuit de l'info 2023 : nous vous invitons  participer  ce test concernant vos habitudes et votre mode de vie.  travers celui-ci, vous obtiendrez une estimation de votre consommation d'eau et de vos missions de CO2. Ensuite, des quiz personnaliss bass sur vos rponses vous fourniront des informations complmentaires. N'hsitez pas  explorer tous les thmes dbloqus au cours de votre parcours, et  dcouvrir les 4 easter eggs que nous avons intgrs. Notre objectif est de sensibiliser  l'impact de nos actions  petite chelle, tout en dmystifiant les ides prconues et en mettant en lumire les principaux polluants  l'chelle mondiale, afin de mieux comprendre les enjeux actuels.</p>
                <NavLink
                className={"navlink"}
                to={{
                  pathname: "/form",
                }}
              >
                Commencer
              </NavLink>
            </main>
        </div>
    );
};

export default HomePage;